<template>
  <div>
    <section class="container">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Event Ticket Applications</h1>
        </div>
      </div>
    </section>

    <section class="container section">
      <div class="columns is-centered content">
        <div class="column is-6">
          <p>
            Please fill out the form below to register your interest to attend
            TEDxAuckland at Whoa! Studios on May 1st. With only 160 seats
            available - tickets are extremely limited, so make sure to register
            your interest as soon as possible.
          </p>
          <p>
            All applicants will receive an email notifying them if their
            application is successful, or if tickets have run out. We apologise
            in advance to anyone who misses out - but you will still be able to
            view the talks afterwards, and there are 2 more chances to attend a
            TEDxAuckland event this year.
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="applyForTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">Application Details</h2>
            <div class="content">
              <p>
                This year we have a limited number of tickets available so we are using this data to ensure we have a diverse audience rather than first come first served.
              </p>
            </div>
            <ApplicationQuestions
              class="section-spacing"
              :details="applicationQuestions"
              :$v="$v"
            />

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="Apply For Ticket"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/components/PersonInfo";
import ApplicationQuestions from "@/components/ApplicationQuestions";
import TicketService from "@/services/TicketService";
import personValidations from "@/validations/PersonValidations";
import applicationQuestionsValidations from "@/validations/ApplicationQuestionsValidations";

export default {
  name: "apply",
  components: {
    PersonInfo,
    ApplicationQuestions
  },
  data() {
    return {
      person: this.createFreshPerson(),
      applicationQuestions: this.createFreshApplicationQuestions(),
      application: this.createFreshTicketQuestions()
    };
  },
  validations: {
    person: personValidations,
    applicationQuestions: applicationQuestionsValidations
  },
  methods: {
    applyForTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        TicketService.submitApplication({
          person: {
            ...this.person,
            data: this.applicationQuestions
          },
          personDetail: this.applicationQuestions
        })
          .then(res => {
            this.$router.push({
              name: "application-status",
              params: { id: res.data.application_id }
            });
            this.person = this.createFreshPerson();
            this.applicationQuestions = this.createFreshApplicationQuestions();
            this.application = this.createFreshTicketQuestions();
          })
          .catch(e => {
            console.log("ERROR", e);
          });
      }
    },

    createFreshPerson() {
      return {
        name: "",
        email: ""
      };
    },

    createFreshApplicationQuestions() {
      return {
        age: "",
        gender: "",
        eventsAttended: [],
        occupation: "",
        industry: "",
        educationLevel: ""
      };
    },

    createFreshTicketQuestions() {
      return {
        organisation: "",
        suburb: "",
        area: "",
        founded: "",
        conversationStarters: {
          one: "",
          two: "",
          three: ""
        }
      };
    }
  }
};
</script>
